
import copy from "@/lib/copy";
import Vue from "vue";
import { Component } from "vue-property-decorator";
@Component
export default class Calculator extends Vue {
  output = "0";
  sign = "";
  numberCache = 0;

  input(event: MouseEvent) {
    const button = event.target as HTMLButtonElement;
    const input = button.textContent!;
    if (this.output.length === 16) {
      return;
    }
    if (this.output === "0") {
      if (input !== ".") {
        this.output = input;
      } else {
        this.output += input;
      }
      return;
    }
    if (this.output.indexOf(".") >= 0 && input === ".") {
      return;
    }
    this.output += input;
  }

  calculate(event: MouseEvent) {
    const button = event.target as HTMLButtonElement;
    const input = button.textContent!;
    const number = parseFloat(copy(this.output));
    if (this.numberCache != 0) {
      this.numberCache = this.cal(this.sign, this.numberCache, number);
    } else {
      this.numberCache = number;
    }
    this.output = "0";
    this.sign = input;
  }
  equal() {
    if (this.output === "0" && this.numberCache != 0) {
      this.output = this.numberCache.toString();
      this.sign = "";
      return;
    } else if (this.sign === "") {
      this.sign = "";
      return;
    }
    const number = parseFloat(copy(this.output));
    this.output = this.cal(this.sign, this.numberCache, number).toString();
    this.numberCache = 0;
    this.sign = "";
  }
  cal(key: string, a: number, b: number) {
    if (key === "+") {
      a = a + b;
    } else if (key === "-") {
      a = a - b;
    } else if (key === "×") {
      a = a * b;
    } else if (key === "÷") {
      a = a / b;
    }
    return a;
  }

  backSpace() {
    if (this.output.length === 1) {
      this.output = "0";
    } else {
      this.output = this.output.slice(0, -1);
    }
  }
  clear() {
    this.output = "0";
  }
  save() {
    if (this.sign === "" && this.output != "0") {
      this.$emit("update:value", this.output);
      this.$emit("submit", this.output);
      this.output = "0";
    } else {
      alert("金额为空");
    }
  }
}
